import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, NaturezaRubrica } from '../../../../models/dtos/mfp/naturezaRubrica';

interface NaturezaRubricaEditViewProps extends EditViewProps<NaturezaRubrica> {}

const NaturezaRubricaEditView: FC<NaturezaRubricaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<NaturezaRubrica>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Manutenção de Natureza da Rubrica">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 6), required: true } }}
              id="codigo"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o código da natureza da rubrica."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 255 } }}
              id="nome"
              isPrependLabel={true}
              label="Nome"
              labelWidth={defaultWidth}
              placeholder="Informe o nome da natureza da rubrica."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 1000 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a descrição da natureza da rubrica."
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default NaturezaRubricaEditView;
